<template>
  <div id="collection-detail-component" v-if="detail">
    <div class="top-container">
      <div class="top-wrapper">
        <div class="title-container">
          <h1 class="title">{{ detail.name }}</h1>
          <font-awesome-icon
            class="icon"
            :icon="['fas', 'edit']"
          ></font-awesome-icon>
        </div>
        <div class="buttons-container">
          <el-button type="success" @click="onStartProjectClick"
            >Start Project</el-button
          >
          <el-button type="info" @click="onAnalysisClick">Analysis</el-button>
          <el-button type="info" @click="onUsersClick">Users</el-button>
          <el-button type="info" @click="onShareClick">Share</el-button>
          <el-button type="info" @click="onDeleteClick">Delete</el-button>
        </div>
      </div>

      <h2 class="info">{{ detail.description }} {{ hashtags }}</h2>
    </div>

    <div class="cards-container">
      <el-row :gutter="60">
        <el-col
          v-for="(item, index) in detail.collection_fabrics"
          :key="index"
          :span="8"
        >
          <FabricCardEditable
            :data="item"
            @edit="onCardEditClick"
            @add="onCardAddClick"
          ></FabricCardEditable>
        </el-col>
      </el-row>
    </div>

    <el-dialog title="Start Project" v-model="dialogStartProjectVisible">
      <template #footer>
        <span class="dialog-footer">
          <el-button type="info" @click="dialogStartProjectVisible = false">
            Cancel
          </el-button>
          <el-button type="success" @click="onStartProjectConfirm">
            Done
          </el-button>
        </span>
      </template>
    </el-dialog>

    <el-dialog title="Share" v-model="dialogShareVisible">
      <template #footer>
        <span class="dialog-footer">
          <el-button type="info" @click="dialogShareVisible = false">
            Cancel
          </el-button>
          <el-button type="success" @click="onShareConfirm">
            Done
          </el-button>
        </span>
      </template>
    </el-dialog>

    <el-dialog title="Users" v-model="dialogUsersVisible">
      <template #footer>
        <span class="dialog-footer">
          <el-button type="info" @click="dialogUsersVisible = false">
            Cancel
          </el-button>
          <el-button type="success" @click="onUsersConfirm">
            Done
          </el-button>
        </span>
      </template>
    </el-dialog>

    <el-dialog v-model="dialogDeleteVisible">
      <h3>Are yoy sure you want to delete?</h3>
      <template #footer>
        <span class="dialog-footer">
          <el-button type="info" @click="dialogDeleteVisible = false">
            Cancel
          </el-button>
          <el-button type="success" @click="onDeleteConfirm">
            Done
          </el-button>
        </span>
      </template>
    </el-dialog>

    <!--For Fabric Card Editable Component-->
    <el-dialog title="Add To Collection" v-model="dialogAddToCollectionVisible">
      <template #footer>
        <span class="dialog-footer">
          <el-button type="info" @click="dialogAddToCollectionVisible = false">
            Cancel
          </el-button>
          <el-button type="success" @click="onAddToCollectionConfirm">
            Done
          </el-button>
        </span>
      </template>
    </el-dialog>

    <el-dialog
      :title="`Edit Price: ${editData.name}`"
      v-model="dialogEditVisible"
    >
      <div class="edit-main">
        <div class="input-item">
          <p class="label">Price:</p>
          <el-input v-model="editData.price" type="number" clearable></el-input>
        </div>
      </div>
      <template #footer>
        <span class="dialog-footer">
          <el-button type="info" @click="dialogEditVisible = false">
            Cancel
          </el-button>
          <el-button type="success" @click="onEditConfirm">
            Save
          </el-button>
        </span>
      </template>
    </el-dialog>
  </div>
</template>

<script lang="ts">
import {
  defineComponent,
  ref,
  reactive,
  toRefs,
  computed,
  onMounted
} from "vue";
import { useStore } from "vuex";
import { useRouter } from "vue-router";

import { loading } from "@/utils";
import FabricCardEditable from "@/components/FabricCardEditable.vue";

import {
  GetCollection,
  PostCollectionUpdateFabricPrice
} from "@/typings/store";
import {
  CollectionFabrics,
  CollectionUpdateFabricPriceParams
} from "@/typings/collection";
import { ElNotification } from "element-plus";

const Component = defineComponent({
  name: "CollectionDetail",
  components: {
    FabricCardEditable
  },
  props: {
    id: {
      type: String,
      default: ""
    }
  },
  setup(props) {
    const router = useRouter();
    const store = useStore();

    const { id } = toRefs(props);
    const dialogStartProjectVisible = ref(false);
    const dialogUsersVisible = ref(false);
    const dialogShareVisible = ref(false);
    const dialogDeleteVisible = ref(false);
    const dialogAddToCollectionVisible = ref(false);
    const dialogEditVisible = ref(false);
    const editData = reactive({
      id: 0,
      name: "",
      price: 0
    });

    const detail = computed(() => store.state.collection.detail);
    const hashtags = computed(() => {
      let str = "";
      if (detail.value.year) str += `#${detail.value.year}, `;
      if (detail.value.season) str += `#${detail.value.season}, `;
      if (detail.value.hash_tag) {
        str += detail.value.hash_tag
          .split(",")
          .map((it: string) => `#${it}`)
          .join(",");
      }
      return str;
    });

    const init = async () => {
      const loadingInstance = loading();
      try {
        await store.dispatch(GetCollection, id.value);
      } finally {
        loadingInstance.close();
      }
    };

    onMounted(async () => await init());

    const onStartProjectClick = () => {
      dialogStartProjectVisible.value = true;
    };
    const onAnalysisClick = () => {
      router.push({ name: "CollectionAnalysis", params: { id: id.value } });
    };
    const onUsersClick = () => {
      dialogUsersVisible.value = true;
    };
    const onShareClick = () => {
      dialogShareVisible.value = true;
    };
    const onDeleteClick = () => {
      dialogDeleteVisible.value = true;
    };

    const onCardEditClick = (data: CollectionFabrics) => {
      editData.id = data.fabric.id;
      editData.name = data.fabric.name;
      editData.price = data.price;
      dialogEditVisible.value = true;
    };
    const onCardAddClick = () => {
      dialogAddToCollectionVisible.value = true;
    };

    const onStartProjectConfirm = () => {
      console.log("start project");
      dialogStartProjectVisible.value = false;
    };
    const onUsersConfirm = () => {
      console.log("users");
      dialogUsersVisible.value = false;
    };
    const onShareConfirm = () => {
      console.log("share");
      dialogShareVisible.value = false;
    };
    const onDeleteConfirm = () => {
      console.log("delete");
      dialogDeleteVisible.value = false;
    };
    const onAddToCollectionConfirm = () => {
      console.log("add to collection");
      dialogAddToCollectionVisible.value = false;
    };
    const onEditConfirm = async () => {
      const params: CollectionUpdateFabricPriceParams = {
        collection_id: id.value,
        fabric_id: editData.id,
        price: editData.price
      };
      try {
        await store.dispatch(PostCollectionUpdateFabricPrice, params);
        dialogEditVisible.value = false;
        ElNotification({
          title: "Success",
          type: "success",
          customClass: "message",
          message: `Edit fabric ${editData.name} price to ${editData.price} successful.`
        });
      } finally {
        await init();
      }
    };

    return {
      dialogStartProjectVisible,
      dialogUsersVisible,
      dialogShareVisible,
      dialogDeleteVisible,
      dialogAddToCollectionVisible,
      dialogEditVisible,
      editData,
      detail,
      hashtags,

      onStartProjectClick,
      onAnalysisClick,
      onUsersClick,
      onShareClick,
      onDeleteClick,

      onCardEditClick,
      onCardAddClick,
      onStartProjectConfirm,
      onUsersConfirm,
      onShareConfirm,
      onAddToCollectionConfirm,
      onDeleteConfirm,
      onEditConfirm
    };
  }
});

export default Component;
</script>

