  background-color $color-background
  padding 30px 10%

  .top-container
    height 80px
    margin-bottom 10px
    .top-wrapper
      flex(space-between)
      margin-bottom 10px
      .title-container
        flex(flex-start)
        .title
          font-size $font-size-title
          font-weight 700
          margin-right 15px
