          width 25px
          height 25px
          cursor pointer
      .buttons-container
        flex(flex-start)
        :deep() .el-button
          margin-right 10px
    .info
      font-size $font-size-lg3x
      font-weight 500


  .input-item
    flex(flex-start, center)
    .label
      min-width 50px
      text-align start
      font-size $font-size-large
      font-weight 500
      padding 0 8px

  .cards-container
    height calc(100vh - 180px)
    overflow-y scroll
    :deep() .el-row
      margin-right 0 !important
      margin-left 0 !important
